<template>
<div>
    <h1>Lista de Personas</h1>
    <ul>
        <li v-for="person in personsList" :key="person.PersonId">
            {{person.nombre + ' - ' + person.rol}}
        </li>
    </ul>
</div>
</template>

<script>
export default {
    data(){
        return {
            personsList: []
        }
    },
    mounted(){
        this.axios.get('/api/persons').then(res => {
            this.personsList = res.data.persons;
        })
    }
}
</script>